<template>
    <div>
        <div class="advertisingBox" v-show="isShowHomePageWindow&&isHttpEnd&&hasShowThemeAlert" @click="closeWindowBtn(false)">
<!--        <div class="advertisingBox" v-show="isShowHomePageWindow&&isHttpEnd" @click="closeWindowBtn(false)">-->
            <!--广告图片-->
            <div v-show="!isShowCouponList" class="advertisingImgBox" @click.stop="skipLinkFun(imgUrlData)">
                <div class="advertisingImg">
                    <img v-lazy="imgUrlData.PIC_URL" :key="imgUrlData.PIC_URL" alt="">
                </div>
                <div class="closeBtn">
                    <img src="@/assets/images/home/pop/home_Pop_btn_closed.png" alt="" @click.stop="closeWindowBtn(false)">
                </div>
            </div>
            <!--优惠券列表-->
            <div class="couponBox" v-show="isShowCouponList">
                <div class="couponTitle">
                    <img src="@/assets/images/home/pop/home_Pop_img.png" alt="">
                </div>
                <div class="couponList" :class="isMoreThree?'overScroll':''">
                    <div class="couponItem" v-for="(couponItem,index) in couponList" :key="'couponItem'+index">
                        <div class="couponInfo">
                            <div class="couponLeft">
                                <div class="couponMoney">
                                    <span class="couponMoneyIcon">￥</span>
                                    <span class="couponMoneyNum">{{couponItem.couponTotal}}</span>
                                </div>
                                <div class="couponShopInfo">
                                    <div class="couponShop">{{couponItem.shopName}}</div>
                                    <div class="couponDesc">{{couponItem.name}}</div>
                                </div>
                            </div>
                            <div class="receiveBtn" v-if="couponItem.isPicked!=1" @click.stop="receiveBtn(couponItem)">立即领取</div>
                            <div class="useBtn" v-else-if="couponItem.isPicked==1" @click.stop="goToShopPage(couponItem.shopCode)">立即使用</div>
                        </div>
                    </div>
                </div>
                <div class="closeBtn">
                    <img src="@/assets/images/home/pop/home_Pop_btn_closed.png" alt="" @click="closeWindowBtn(false)">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {getBannerListService,queryPopupCouponListService,receiveBtnService} from "../../services/goodsService";
    import {skipLink} from "../../utils/skipLinkManage";
    import { mapActions, mapState } from "vuex";
    import {Toast,Dialog} from "vant";
    export default {
        name: "homePageWindow",
        data(){
            return{
                imgUrlData:[], // 图片弹窗数据
                isShowCouponList:true, // 是否显示优惠券列表
                isMoreThree:false, //是否有多个优惠券
                couponList:[], // 优惠券列表
                isHttpEnd:false, //是否请求完成
            }
        },
        created(){

        },
        mounted(){
            if (this.isShowHomePageWindow) {
                this.queryPopupCouponList();
            }
        },
        props:['isShowHomePageWindow','areaList'],
        watch: {},
        computed: mapState({
            pageNumber: state => state.productPage.shopGoodsPageNumber,
            hasShowThemeAlert: state => state.productPage.hasShowThemeAlert,
        }),
        methods:{
            //关闭或打开首页弹窗
            closeWindowBtn(val){
              this.$emit('childByvalue',val)
            },
            /**
             * 获取首页广告弹窗列表
             * AREA_CODE    String   区域代码
             * ADVERT_AREA  Integer  广告区域代码 8
             * OWNER_CODE   String   用户代码
             */
            getBannerList(){
                let paramData = {
                    AREA_CODE:this.$userInfo.AREA_CODE,
                    OWNER_CODE:this.$userInfo.OWNER_CODE,
                    ADVERT_AREA:8
                }
                getBannerListService(paramData).then(res => {
                    if (res.length>0){
                        this.imgUrlData = res[0]
                        this.isHttpEnd = true;
                    }
                }).catch(error => {
                });
            },
            /**
            * 获取首页优惠券弹窗列表
            * areaCode    String   小区代码
            * roomCode    String   房间代码
            * ownerCode   String   业主代码
            */
            queryPopupCouponList(){
                let paramData = {
                    areaCode:this.$userInfo.AREA_CODE,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    roomCode : this.$userInfo.ROOM_CODE
                }
                queryPopupCouponListService(paramData).then(res => {
                    if (res.resultCode == 0){
                        this.couponList = res.data.date;
                        if (this.couponList.length == 0){
                            this.getBannerList();
                            this.isShowCouponList = false;
                            return
                        }else if (this.couponList.length >= 3){
                            this.isMoreThree = true;
                        }
                        this.isHttpEnd = true;
                        this.isShowCouponList = true;
                    }
                }).catch(error => {
                });
            },
            /**
             * 领取首页优惠券
             * areaCode    String   小区代码
             * id    String   优惠券代码
             * ownerCode   String   业主代码
             * roomCode  Number      房间CODE
             */
            receiveBtn(couponItem){
                if(!this.$userInfo.ROOM_CODE){
                    this.closeWindowBtn(false);
                    Dialog.confirm({
                    message: "您未绑定房间，是否前往绑定？",
                    confirmButtonText: "前往"
                    }).then(() => {
                        //跳转到绑定房产页面
                    this.$router.push("selectArea");
                    }).catch(() => {
                    });
                    return
                }
                let paramData ={
                    id : couponItem.id,
                    ownerCode : this.$userInfo.OWNER_CODE,
                    areaCode : this.$userInfo.AREA_CODE,
                    roomCode: this.$userInfo.ROOM_CODE
                }
                console.log(paramData)
                receiveBtnService(paramData).then(res => {
                    if (res.resultCode == 0){
                        // this.queryPopupCouponList();
                        couponItem.isPicked = 1;
                        Toast("领取优惠券成功")
                    }else{
                        Toast(res.msg)
                    }
                }).catch(error => {
                });
            },
            //点击图片广告进行跳转页面
            skipLinkFun(x){
                this.closeWindowBtn(false);
                skipLink(this,this.areaList.length,x);
            },
            //去店铺首页
            goToShopPage(shopCode){
                this.closeWindowBtn(false)
                this.$router.push({
                    name: 'shopPage',
                    params: {
                        shopCode: shopCode
                    }
                })
            }
        }
    }
</script>

<style scoped lang="scss">
    .advertisingBox{
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: rgba(0,0,0,0.6);
        box-sizing: border-box;
        z-index: 100;
        .advertisingImgBox{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            .advertisingImg{
                width: 325px;
                height: 345px;
                overflow: hidden;
                margin: 0 auto;
                border-radius: 4px;
                img{
                    width: 325px;
                    height: 345px;
                }
            }
        }

        .couponBox{
            width: 309px;
            margin: 0 auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            .couponTitle{
                width: 100%;
                height: 132.56px;
                /*display: flex;*/
                img{
                    width: 100%;
                    height: 132.56px;
                }
            }
            .couponList{
                width: 100%;
                max-height: 260px;
                box-sizing: border-box;
                padding: 20px 11px;
                background: #FD2853;
                border-radius: 0 0 6px 6px;
                .couponItem{
                    width: 100%;
                    height: 80px;
                    background: #FFFFFF;
                    border-radius: 4px;
                    box-sizing: border-box;
                    padding: 4px;
                    margin-bottom: 10px;
                    background: url("../../assets/images/home/pop/home_Pop_coupon_bg.png") no-repeat;
                    -webkit-background-size: contain;
                    background-size: 100% 100%;
                    .couponInfo{
                        width: 100%;
                        height: 100%;
                        /*border: 1px solid #FFCBBD;*/
                        border-radius: 2px;
                        box-sizing: border-box;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        padding: 0 5px 0 0;
                        .couponLeft{
                            width: 200px;
                            height: 100%;
                            display: flex;
                            align-items: center;
                            .couponMoney{
                                width: 80px;
                                text-align: center;
                                .couponMoneyIcon{
                                    font-family: PingFangSC-Semibold;
                                    font-size: 14px;
                                    color: #FF5050;
                                    letter-spacing: -0.07px;
                                }
                                .couponMoneyNum{
                                    display: inline-block;
                                    font-family: PingFangSC-Semibold;
                                    font-size: 32px;
                                    color: #FF5050;
                                    letter-spacing: -0.21px;
                                }
                            }
                            .couponShopInfo{
                                margin-left: 10px;
                                .couponShop{
                                    width: 100px;
                                    overflow: hidden;
                                    text-overflow:ellipsis;
                                    white-space: nowrap;
                                    font-family: PingFangSC-Medium;
                                    font-size: 14px;
                                    color: #4A4A4A;
                                    letter-spacing: -0.04px;
                                }
                                .couponDesc{
                                    width: 100px;
                                    overflow: hidden;
                                    text-overflow:ellipsis;
                                    white-space: nowrap;
                                    font-family: PingFangSC-Regular;
                                    font-size: 12px;
                                    color: #4A4A4A;
                                    letter-spacing: -0.04px;
                                }
                            }

                        }
                        .receiveBtn{
                            width: 85px;
                            height: 33px;
                            box-sizing: border-box;
                            background-image: linear-gradient(0deg, #FD2853 24%, #FF6A6A 96%);
                            border: 1px solid #FF6262;
                            box-shadow: 0 2px 6px 0 rgba(255,81,44,0.60);
                            font-family: PingFangSC-Regular;
                            font-size: 14px;
                            color: #FFFFFF;
                            letter-spacing: 0;
                            text-align: center;
                            text-shadow: 0 2px 4px #C55529;
                            line-height: 30px;
                            border-radius: 30px;
                        }
                        .useBtn{
                            width: 85px;
                            height: 33px;
                            box-sizing: border-box;
                            background-image: linear-gradient(0deg, #FF9A07 3%, #FFD320 100%);
                            border: 1px solid #FFD43E;
                            box-shadow: 0 2px 6px 0 rgba(255,176,44,0.60);
                            border-radius: 30px;
                            font-family: PingFangSC-Regular;
                            font-size: 14px;
                            color: #FFFFFF;
                            letter-spacing: 0;
                            text-align: center;
                            text-shadow: 0 2px 4px #BB491C;
                            line-height: 30px;
                        }
                    }
                }

                .couponItem:last-child{
                    margin-bottom: 0px;
                }
            }
            .overScroll{
                overflow-y: scroll;
            }
        }

        .closeBtn{
            width: 40px;
            height: 40px;
            display: flex;
            margin: 17px auto 0;
            img {
                width: 100%;
            }
        }
    }
</style>
