    //获取首页banner数据 isBootAd: 是底部广告
    getBannerData: function (isBootAd = false) {
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE,
      };
      if (isBootAd) {
        reqData.ADVERT_AREA = 6;
        reqData.OWNER_CODE = this.$userInfo.OWNER_CODE;
      }

      getHomePageBannerService(reqData)
        .then(res => {

          if (isBootAd) {
            //底部广告
            this.adPositions = res;
          } else {
            //顶部滚动banner
            if (res.length == 0) {
              this.headerBanners = [{"PIC_URL": bannerDefaultImg}];
            } else {
              this.headerBanners = res;
            }
          }
          this.refreshing = false;
        })
        .catch(error => {
          //this.showMsg(error);

          console.log("【Error】getBannerData：" + error);
          this.refreshing = false;

          this.networkError = true;
        });
    },

    //获取首页banner数据和底部广告
    getBannerListMerged: function () {
      var _this = this;
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE,
        ADVERT_AREA: "4,6",
        OWNER_CODE: this.$userInfo.OWNER_CODE
      };

      getBannerListMergedService(reqData)
        .then(res => {
          //顶部滚动banner
          if (res.ADVERT_1.length == 0) {
            this.headerBanners = [{"PIC_URL": bannerDefaultImg}];
          } else {
            //替换url
            for (let i = 0; i < res.ADVERT_1.length; i++) {
              let item = res.ADVERT_1[i];
              if (i%2==0) {
                item.PIC_URL = item.PIC_URL.replace('xwjfile.juhaolian.cn', 'xwjfilea.juhaolian.cn');
              }

              //本地化处理
              Vue.prototype.$isHasLocalImage(item.PIC_URL, function (localUrl) {
                item.PIC_URL = localUrl;
                console.log("首页-顶部banner： return url["+i+"] = "+ item.PIC_URL);
                //_this.$forceUpdate();
              });
            }

            this.headerBanners = res.ADVERT_1;
          }

          //底部广告
          //替换url
          for (let i = 0; i < res.ADVERT_2.length; i++) {
            let item = res.ADVERT_2[i];
            if (i%2==0) {
              item.PIC_URL = item.PIC_URL.replace('xwjfile.juhaolian.cn', 'xwjfilea.juhaolian.cn');
            }

            //本地化处理
            Vue.prototype.$isHasLocalImage(item.PIC_URL, function (localUrl) {
              item.PIC_URL = localUrl;
              console.log("首页-底部广告： return url["+i+"] = "+ item.PIC_URL);
              //_this.$forceUpdate();
            });
          }
          this.adPositions = res.ADVERT_2;

          //恢复下拉刷新状态位
          this.refreshing = false;
        })
        .catch(error => {
          console.log("【Error】getBannerListMerged：" + error);
          this.refreshing = false;

          this.networkError = true;
        });
    },

    //获取应用列表
    getApplyList: function () {
      let _this = this;
      let reqData = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        AREA_CODE: this.$userInfo.AREA_CODE
      };

      getApplyService(reqData)
        .then(res => {
          let hasOpenDoor = false;
          let position = 0;
          res.forEach((item, index) => {
            // 如果有手机开门,需要从列表中删除
            if ("0001" == item.APP_CODE) {
              hasOpenDoor = true;
              position = index;
            }

            //替换url
            if (index%2 == 0) {
              item.PIC_URL = item.PIC_URL.replace('xwjfile.juhaolian.cn', 'xwjfilea.juhaolian.cn');
            }

            //本地化处理
            Vue.prototype.$isHasLocalImage(item.PIC_URL, function (localUrl) {
              item.PIC_URL = localUrl;
              // console.log("首页-应用区图标： return url["+index+"] = "+ item.PIC_URL);
              //_this.$forceUpdate();
            });

            item.unpayBillNum = 0;
          });

          //保存手机开门状态，用于tab bar控制手机开门入口是否显示
          this.$store.dispatch('setOpenDoorFlag', hasOpenDoor);

          if (hasOpenDoor) {
            res.splice(position, 1);
            //有手机开门功能时显示引导页
            //$scope.showBootPage();
          }
          //最多显示两行应用，一行5个
          let maxItems = 10;
          let singleLineMax = 5;
          if (this.$curTheme == "1") {
            //大字模式
            maxItems = 8;
            singleLineMax = 4;
          }
          let moreItem = {
            PIC_URL: this.moreApplyImg,
            APP_NAME: "更多服务",
            APP_CODE: "9999"
          };
          if (res.length > maxItems) {
            res.splice(maxItems - 1, res.length - maxItems + 1, moreItem);
          } else if (res.length > singleLineMax && res.length < maxItems) {
            //超过一行，不足两行时，显示一行+更多服务
            res.splice(singleLineMax - 1, res.length - singleLineMax + 1, moreItem);
          }

          this.apply = res;
          this.getUnpayBillNum();
        })
        .catch(error => {
          //this.showMsg(error);
          console.log("【Error】getApplyList：" + error);
          this.networkError = true;
        });
    },
    //获取未缴费账单
    getUnpayBillNum: function () {
      let reqData = {
        userCode: this.$userInfo.OWNER_CODE
      };
      getUnpayBillNumService(reqData)
        .then(res => {
          if (res.resultCode == 200) {
            if (parseInt(res.data) > 0) {
              let data = this.apply;
              data.forEach((item, index) => {
                // 更新缴费的未缴账单数目
                if ("0010" == item.APP_CODE) {
                  item.unpayBillNum = parseInt(res.data);
                }
              });
            }
          }
        })
        .catch(error => {
          //this.showMsg(error);
          console.log("【Error】getUnpayBillNum：" + error);
          this.networkError = true;
        });
    },
